/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable @typescript-eslint/no-explicit-any */
'use client';

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/hooks/useAuth';
import {
    Calendar,
    Plus,
    Search,
    Filter,
    Edit,
    Eye,
    Trash2,
    MapPin,
    Clock,
    Tag,
    Globe,
    Users,
    DollarSign
} from 'lucide-react';

// Local interface to match your Event structure
interface Event {
    eventId: number;
    title: string;
    description: string;
    eventDate: string | number | Date;
    startDateTime: string;
    endDateTime: string;
    location?: string;
    isOnline: boolean;
    isPublished: boolean;
    maxCapacity?: number;
    categoryName?: string;
    venueName: string;
    ticketsSold: number;
    availableTickets: number;
    revenue: number;
    currency: string;
    status: string;
    imageUrl?: string;
}

const EventsListPage = () => {
    const router = useRouter();
    const { user, isOrganizer } = useAuth();
    const [loading, setLoading] = useState(true);
    const [events, setEvents] = useState<Event[]>([]);
    const [filteredEvents, setFilteredEvents] = useState<Event[]>([]);
    const [searchQuery, setSearchQuery] = useState('');
    const [filterStatus, setFilterStatus] = useState<'all' | 'published' | 'draft'>('all');
    const [error, setError] = useState('');

    useEffect(() => {
        if (user && isOrganizer) {
            fetchEvents();
        } else if (user && !isOrganizer) {
            router.push('/');
        }
    }, [user, isOrganizer, router]);

    useEffect(() => {
        filterEvents();
    }, [events, searchQuery, filterStatus]);

    const fetchEvents = async () => {
        try {
            setLoading(true);
            setError('');

            // Fetch events from your real API endpoint
            const response = await fetch('http://localhost:5251/api/events/my-events', {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}`
                }
            });

            if (!response.ok) {
                throw new Error('Failed to fetch events');
            }

            const eventsData = await response.json();
            console.log('Fetched events:', eventsData);

            // Map the response to match our Event interface with proper null checks
            const mappedEvents: Event[] = eventsData.map((event: any) => ({
                eventId: event.eventId,
                title: event.title || '',
                description: event.description || '',
                eventDate: event.eventDate,
                startDateTime: event.startDateTime || event.eventDate,
                endDateTime: event.endDateTime || event.eventDate,
                location: event.location || '',
                isOnline: event.isOnline || false,
                isPublished: event.isPublished || false,
                maxCapacity: event.maxCapacity || 0,
                categoryName: event.categoryName || '',
                venueName: event.venueName || (event.isOnline ? 'Virtual Event' : 'TBD'),
                ticketsSold: event.ticketsSold || 0,
                availableTickets: event.availableTickets || event.maxCapacity || 0,
                revenue: event.revenue || 0,
                currency: event.currency || 'RM',
                status: event.isPublished ? 'Active' : 'Draft',
                imageUrl: event.imageUrl || ''
            }));

            setEvents(mappedEvents);
        } catch (error) {
            console.error('Error fetching events:', error);
            setError('Failed to load events');

            // Fallback to mock data if API fails (for development)
            const mockEvents: Event[] = [
                {
                    eventId: 1,
                    title: "Tech Conference 2025",
                    description: "Annual technology conference featuring latest innovations in AI, blockchain, and web development.",
                    eventDate: "2025-07-15T09:00:00Z",
                    startDateTime: "2025-07-15T09:00:00Z",
                    endDateTime: "2025-07-15T17:00:00Z",
                    location: "KLCC Convention Center",
                    isOnline: false,
                    isPublished: true,
                    maxCapacity: 200,
                    categoryName: "Technology",
                    venueName: "KLCC Convention Center",
                    ticketsSold: 150,
                    availableTickets: 50,
                    revenue: 15000,
                    currency: "RM",
                    status: "Active",
                    imageUrl: ""
                },
                {
                    eventId: 2,
                    title: "Digital Marketing Workshop",
                    description: "Learn the latest digital marketing strategies and tools to grow your business online.",
                    eventDate: "2025-06-20T14:00:00Z",
                    startDateTime: "2025-06-20T14:00:00Z",
                    endDateTime: "2025-06-20T18:00:00Z",
                    location: "Online",
                    isOnline: true,
                    isPublished: false,
                    maxCapacity: 100,
                    categoryName: "Business",
                    venueName: "Virtual Event",
                    ticketsSold: 45,
                    availableTickets: 55,
                    revenue: 2250,
                    currency: "RM",
                    status: "Draft",
                    imageUrl: ""
                }
            ];
            setEvents(mockEvents);
        } finally {
            setLoading(false);
        }
    };

    const filterEvents = () => {
        let filtered = events;

        // Filter by search query with proper null/undefined checks
        if (searchQuery.trim()) {
            filtered = filtered.filter(event => {
                const title = event.title || '';
                const description = event.description || '';
                const categoryName = event.categoryName || '';

                return title.toLowerCase().includes(searchQuery.toLowerCase()) ||
                    description.toLowerCase().includes(searchQuery.toLowerCase()) ||
                    categoryName.toLowerCase().includes(searchQuery.toLowerCase());
            });
        }

        // Filter by status
        if (filterStatus !== 'all') {
            filtered = filtered.filter(event =>
                filterStatus === 'published' ? event.isPublished : !event.isPublished
            );
        }

        setFilteredEvents(filtered);
    };

    const handlePublishEvent = async (eventId: number, currentStatus: boolean) => {
        try {
            const endpoint = currentStatus ? 'unpublish' : 'publish';
            const response = await fetch(`http://localhost:5251/api/events/${eventId}/${endpoint}`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
                    'Content-Type': 'application/json'
                }
            });

            if (!response.ok) {
                throw new Error(`Failed to ${endpoint} event`);
            }

            console.log(`Event ${eventId} ${endpoint}ed successfully`);

            // Update local state
            setEvents(events.map(event =>
                event.eventId === eventId
                    ? { ...event, isPublished: !currentStatus, status: !currentStatus ? 'Active' : 'Draft' }
                    : event
            ));
        } catch (error) {
            console.error('Error updating event status:', error);
            setError(`Failed to ${currentStatus ? 'unpublish' : 'publish'} event`);
        }
    };

    const handleDeleteEvent = async (eventId: number) => {
        if (!confirm('Are you sure you want to delete this event? This action cannot be undone.')) {
            return;
        }

        try {
            const response = await fetch(`http://localhost:5251/api/events/${eventId}`, {
                method: 'DELETE',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}`
                }
            });

            if (!response.ok) {
                throw new Error('Failed to delete event');
            }

            console.log(`Event ${eventId} deleted successfully`);

            // Remove from local state
            setEvents(events.filter(event => event.eventId !== eventId));
        } catch (error) {
            console.error('Error deleting event:', error);
            setError('Failed to delete event');
        }
    };

    const EventCard: React.FC<{ event: Event }> = ({ event }) => (
        <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200 hover:shadow-md transition-shadow">
            <div className="flex justify-between items-start mb-4">
                <div className="flex-1">
                    <h3 className="text-lg font-semibold text-gray-900 mb-2">{event.title}</h3>
                    <p className="text-gray-600 text-sm line-clamp-2">{event.description}</p>
                </div>
                <div className="flex flex-col gap-2 ml-4">
                    <span className={`px-3 py-1 rounded-full text-xs font-medium ${event.isPublished
                        ? 'bg-green-100 text-green-800'
                        : 'bg-yellow-100 text-yellow-800'
                        }`}>
                        {event.isPublished ? 'Published' : 'Draft'}
                    </span>
                    <span className={`px-3 py-1 rounded-full text-xs font-medium ${event.isOnline
                        ? 'bg-blue-100 text-blue-800'
                        : 'bg-purple-100 text-purple-800'
                        }`}>
                        {event.isOnline ? 'Online' : 'In-Person'}
                    </span>
                </div>
            </div>

            <div className="grid grid-cols-2 gap-4 mb-4">
                <div className="flex items-center text-sm text-gray-600">
                    <Calendar className="h-4 w-4 mr-2 flex-shrink-0" />
                    <span>{new Date(event.startDateTime).toLocaleDateString()}</span>
                </div>
                <div className="flex items-center text-sm text-gray-600">
                    <Clock className="h-4 w-4 mr-2 flex-shrink-0" />
                    <span>{new Date(event.startDateTime).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</span>
                </div>
                <div className="flex items-center text-sm text-gray-600">
                    {event.isOnline ? <Globe className="h-4 w-4 mr-2 flex-shrink-0" /> : <MapPin className="h-4 w-4 mr-2 flex-shrink-0" />}
                    <span className="truncate">{event.venueName}</span>
                </div>
                <div className="flex items-center text-sm text-gray-600">
                    <Tag className="h-4 w-4 mr-2 flex-shrink-0" />
                    <span>{event.categoryName || 'Uncategorized'}</span>
                </div>
            </div>

            {/* Event Stats */}
            <div className="flex justify-between items-center p-3 bg-gray-50 rounded-lg mb-4">
                <div className="text-center">
                    <p className="text-lg font-bold text-gray-900">{event.ticketsSold}</p>
                    <p className="text-xs text-gray-600">Tickets Sold</p>
                </div>
                <div className="text-center">
                    <p className="text-lg font-bold text-gray-900">{event.availableTickets + event.ticketsSold}</p>
                    <p className="text-xs text-gray-600">Total Capacity</p>
                </div>
                <div className="text-center">
                    <p className="text-lg font-bold text-green-600">{event.currency} {event.revenue.toLocaleString()}</p>
                    <p className="text-xs text-gray-600">Revenue</p>
                </div>
            </div>

            {/* Action Buttons */}
            <div className="flex justify-between items-center">
                <div className="flex gap-2">
                    <button
                        onClick={() => window.open(`/events/${event.eventId}`, '_blank')}
                        className="flex items-center px-3 py-2 text-sm bg-blue-100 text-blue-700 rounded-lg hover:bg-blue-200 transition-colors"
                    >
                        <Eye className="h-4 w-4 mr-1" />
                        View
                    </button>
                    <button
                        onClick={() => router.push(`/organizer/events/${event.eventId}/edit`)}
                        className="flex items-center px-3 py-2 text-sm bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition-colors"
                    >
                        <Edit className="h-4 w-4 mr-1" />
                        Edit
                    </button>
                </div>
                <div className="flex gap-2">
                    <button
                        onClick={() => handlePublishEvent(event.eventId, event.isPublished)}
                        className={`px-4 py-2 text-sm rounded-lg font-medium transition-colors ${event.isPublished
                            ? 'bg-yellow-100 text-yellow-700 hover:bg-yellow-200'
                            : 'bg-green-100 text-green-700 hover:bg-green-200'
                            }`}
                    >
                        {event.isPublished ? 'Unpublish' : 'Publish'}
                    </button>
                    <button
                        onClick={() => handleDeleteEvent(event.eventId)}
                        className="flex items-center px-3 py-2 text-sm bg-red-100 text-red-700 rounded-lg hover:bg-red-200 transition-colors"
                    >
                        <Trash2 className="h-4 w-4 mr-1" />
                        Delete
                    </button>
                </div>
            </div>
        </div>
    );

    if (!user || !isOrganizer) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="text-center">
                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
                    <p className="mt-4 text-gray-600">Loading...</p>
                </div>
            </div>
        );
    }

    if (loading) {
        return (
            <div className="p-8">
                <div className="max-w-7xl mx-auto">
                    <div className="flex items-center justify-center py-12">
                        <div className="text-center">
                            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
                            <p className="mt-4 text-gray-600">Loading your events...</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    return (
        <div className="p-6">
            <div className="max-w-7xl mx-auto">
                {/* Header */}
                <div className="mb-8">
                    <div className="flex justify-between items-center">
                        <div>
                            <h1 className="text-3xl font-bold text-gray-900">My Events</h1>
                            <p className="text-gray-600 mt-1">Manage and track all your events</p>
                        </div>
                        <button
                            onClick={() => router.push('/organizer/events/create')}
                            className="flex items-center px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                        >
                            <Plus className="h-5 w-5 mr-2" />
                            Create Event
                        </button>
                    </div>
                </div>

                {/* Error Message */}
                {error && (
                    <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
                        <p className="text-red-600">{error}</p>
                    </div>
                )}

                {/* Filters and Search */}
                <div className="mb-6 flex flex-col sm:flex-row gap-4">
                    <div className="flex-1">
                        <div className="relative">
                            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-500 h-4 w-4" />
                            <input
                                type="text"
                                placeholder="Search events..."
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent placeholder-gray-600 placeholder:font-medium"
                            />
                        </div>
                    </div>
                    <div className="flex gap-4">
                        <select
                            value={filterStatus}
                            onChange={(e) => setFilterStatus(e.target.value as 'all' | 'published' | 'draft')}
                            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-700 font-medium"
                        >
                            <option value="all">All Events</option>
                            <option value="published">Published</option>
                            <option value="draft">Draft</option>
                        </select>
                    </div>
                </div>

                {/* Stats Summary */}
                <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
                    <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                        <div className="flex items-center">
                            <div className="p-2 bg-blue-100 rounded-lg">
                                <Calendar className="h-6 w-6 text-blue-600" />
                            </div>
                            <div className="ml-4">
                                <p className="text-sm font-medium text-gray-600">Total Events</p>
                                <p className="text-2xl font-bold text-gray-900">{events.length}</p>
                            </div>
                        </div>
                    </div>
                    <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                        <div className="flex items-center">
                            <div className="p-2 bg-green-100 rounded-lg">
                                <Users className="h-6 w-6 text-green-600" />
                            </div>
                            <div className="ml-4">
                                <p className="text-sm font-medium text-gray-600">Tickets Sold</p>
                                <p className="text-2xl font-bold text-gray-900">
                                    {events.reduce((sum, event) => sum + event.ticketsSold, 0).toLocaleString()}
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                        <div className="flex items-center">
                            <div className="p-2 bg-purple-100 rounded-lg">
                                <DollarSign className="h-6 w-6 text-purple-600" />
                            </div>
                            <div className="ml-4">
                                <p className="text-sm font-medium text-gray-600">Total Revenue</p>
                                <p className="text-2xl font-bold text-gray-900">
                                    RM {events.reduce((sum, event) => sum + event.revenue, 0).toLocaleString()}
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                        <div className="flex items-center">
                            <div className="p-2 bg-orange-100 rounded-lg">
                                <Calendar className="h-6 w-6 text-orange-600" />
                            </div>
                            <div className="ml-4">
                                <p className="text-sm font-medium text-gray-600">Published</p>
                                <p className="text-2xl font-bold text-gray-900">
                                    {events.filter(event => event.isPublished).length}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Events Grid */}
                {filteredEvents.length === 0 ? (
                    <div className="text-center py-12 bg-white rounded-lg border border-gray-200">
                        <Calendar className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                        <h3 className="text-lg font-medium text-gray-900 mb-2">
                            {searchQuery || filterStatus !== 'all' ? 'No events found' : 'No events yet'}
                        </h3>
                        <p className="text-gray-600 mb-6">
                            {searchQuery || filterStatus !== 'all'
                                ? 'Try adjusting your search or filter criteria.'
                                : 'Create your first event to get started with EventPro.'
                            }
                        </p>
                        {(!searchQuery && filterStatus === 'all') && (
                            <button
                                onClick={() => router.push('/organizer/events/create')}
                                className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                            >
                                <Plus className="h-4 w-4 mr-2" />
                                Create Your First Event
                            </button>
                        )}
                    </div>
                ) : (
                    <div className="grid gap-6 lg:grid-cols-2">
                        {filteredEvents.map((event) => (
                            <EventCard key={event.eventId} event={event} />
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
};

export default EventsListPage;